@import url('https://fonts.googleapis.com/css?family=Gelasio&display=swap');
@import url('https://fonts.googleapis.com/css?family=Sriracha&display=swap');

*{
    box-sizing: border-box
}

body {
    padding: 0;
    margin: 0;
    background-color: rgb(37, 46, 37);
    color: #ffffff
}

a { 
    text-decoration: none !important; 
    color: #ffffff 
}

/* ACTIVE LINK */
.active {
    background: rgb(37, 46, 37);
    padding: 1vmin;
    border: 1px dashed #ffffff
}

/* HEADER */
header {
    background: aqua;
    padding: 4vmin
}

header .brand {
    color: orangered !important;
    font-size: 6vmin;
    font-family: 'Gelasio', serif
}

header .link_router {
    margin: 5vmin 0;
    font-size: 3vmin;
    text-transform: uppercase;
    transition: all .5s linear
}

header .link_router:hover {
    color: black !important;  
}

.nav-item {
    margin-top: 3vmin
}

/* JUMBOTRON */
.jumbotron {
    color: #ffffff;
    background: transparent
}

.landing_page_btn {
    background: aqua;
    border: 1px dashed;
    transition: all .5s linear
}

/* REPOS */
.repos {
    width: 90vw;
    padding: 4vmin;
    margin: 6vmin 0
}

.repos h1 {
    font-size: 5vmin;
    text-transform: capitalize;
    padding: 2vmin
}

/* REPO */
.repo {
    width: 100%;
    height: auto;
    border: 2px solid;
    padding: 4vmin;
    background: #ffffff;
    border-radius: 20px;
    margin: 6vmin 0;
    color: black;
    display: flex
}

.repo .avatar {
    width: 200px
}

.repo .avatar img {
    padding: 2vmin
}

.repo .info {
    flex-shrink: 1;
    max-width: 100%
}

.repo .info h2, p {
    margin: 3vmin 0
}

.star_issue {
    margin: 3vmin 0;
    width: 100%
}

.star, .issue, .last_submit {
    margin-left: 1vmin;
    padding: 1vmin 2vmin;
    font-size: 2vmin
}

.star, .issue {
    border: 1px solid
}

strong {
    border-left: 1px solid;
    padding: 1vmin
}

/* BAD REQUEST */
.bad_request {
    padding: 4vmin;
    margin: 6vmin 0; 
    font-size: 5vmin;
    text-transform: capitalize
}

.active_page {
    background: red !important
}

.page-link {
    background: transparent
}

/* FOOTER */
footer {
    background: aqua;
    width: 100%;
    padding: 4vmin;
    height: 100px;
    color: #ffffff;
    margin: 0
}

footer .info {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 4vmin
}

/* PAGE NOT FOUND 404 */
.page_not_found{
    width: 97vw;
    padding: 4vmin;
    margin: 6vmin 0;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.page_not_found h1 {
    font-size: 7vmin;
    margin: 2vmin 0;
    font-family: 'Sriracha', cursive
}

/* small-screen meduim-screen */

@media (max-width: 900px) {
    .repo { width: 90vmin }
    .star_issue { width: 70vmin }
}



